import React from "react";
import {
  Grid,
  Segment,
  Label,
  Icon,
  Divider,
  Container,
  Header
} from "semantic-ui-react";
import styles from "../styles/style";
import loadable from "@loadable/component";

const Interest = loadable(() => import("./interest"));
const Education = loadable(() => import("./education"));

const Me = ({ isDownload, handleIsDownload }) => (
  <Grid doubling columns={2} padded>
    <Grid.Column>
      <Segment raised>
        <Label size="huge" as="h1" ribbon color="pink">
          <Icon name="briefcase" />
          My Profile
        </Label>
        <br />

        <Container style={styles.descAboutMe}>
          <Divider hidden />
          <Header as="h3" style={{ fontSize: "2em", color: "pink" }}>
            Who Am I ?
          </Header>
          <p style={{ fontSize: "1.3em" }}>
            Hi there, my name is <b style={{ color: "teal" }}>Haryana</b>.
          </p>
          <p style={{ fontSize: "1.1em" }}>
            An awesome and delightful guy, who has associate's degree from{" "}
            <b>Tri Mitra Karya Mandiri Politeknik</b> in{" "}
            <i>Informatics Engineering</i> and went to{" "}
            <b>Refactory Programming School</b> for further education in
            programming.
            <br />
            <br />
            I'm someone, who has high interest in software development, with
            expertise in some programming language, e.g{" "}
            <strong>HTML, CSS, React JS, and Javascript with Node JS</strong>.
            <br />
            <br />
            furthermore I'm confident with my skills in{" "}
            <b>
              <i>
                JS framework (React Family (React js, Next js, React Native)
                state management with Redux, TypeScript, Express js, Nest js,
                Type ORM/query builder (Squelize, Knex))
              </i>
              , MySQL, Git workflow, wordpress, web design and animations.
            </b>
            <br />
            <br /> I have experiences working on several projects like Dashboard
            app and online financial investments, and startup company in the
            field Travel Planner, And Logistic .
          </p>
          <Divider hidden />
        </Container>
      </Segment>
    </Grid.Column>
    <Grid.Column>
      <Segment style={{ minHeight: "512px" }} raised>
        <Label size="huge" as="h1" color="red" ribbon>
          <Icon name="briefcase" />
          {isDownload ? "Education" : "Interest"}
        </Label>
        <Divider hidden />
        <Container style={styles.me}>
          {isDownload ? <Education /> : <Interest />}
        </Container>
      </Segment>
    </Grid.Column>
  </Grid>
);

export default Me;



